@import "./reset.scss";
@import url('https://fonts.googleapis.com/css?family=VT323');

html {
  font-family:'VT323';
  box-sizing: content-box;
  background-color:black;
  height: 100%;
  overflow-x: hidden;
}

body{
  background-repeat:no-repeat;
  background-position: top;
  background-size:cover;
  overflow-x: hidden;
}

.content{
  overflow-x: hidden; 
}

#dynbackground{
  z-index:-1;
  position:absolute;
  top:0;
  left:0;
  width:100vw;
  &.portrait{
    width:inherit;
    height:100vh;
  }
}

.nav{
  color:white;
  font-size:2.5em;
  height:10%;
  display:flex;
  align-items:center;
  justify-content: center;
  .navbar{
    padding:25px;
    display:flex;
    justify-content: flex-end;
    align-items:center;
    height:50px;
    width:100%;
    a{
      transition:all .25s;
      color:white;
      text-decoration: none;
      &:hover{
        color:yellow;
        border-color:yellow;
      }
    }
    .nav__item:hover{
      margin-left:25px;
      margin-right:25px;
    }
    .nav__item:not(:last-child){
      &:after{
        margin-left:10px;
        margin-right:10px;
        content:"Â·"
      }
    }
    .main{
      padding:5px;
      border:solid 2px white;
    }
    .left{
      margin-right:auto;
    }
  }
}
.page{
  height:100vh;
}

.albumCover{
  display:flex;
  align-items: center;
  justify-content: center;
  transition:all 0.1s;
  opacity: 0.7;
  transform:scale(0.9);
  &:hover{
    opacity: 1;
    transform: scale(1);  
  }
}

.release-showcase{
  margin:100px;
  display:flex;
  align-items:center;
  justify-content: space-around;
  flex-wrap:wrap;
  img{
    margin-top:25px;
    margin-bottom:25px;
    height:350px;
    width:350px;
  }
}

@media only screen and (max-width: 1100px) {
  .nav{
    font-size:1.5em;
  }
  .release-showcase{
    img{
      height:300px;
      width:300px;
    }
  }
}

@media only screen and (max-width: 950px) {
  .release-showcase{
    flex-direction: column;
  }
}